.page {

  &-home {
    .header {
      background-color: var(--white);
    }

    .request-form {

      .form__title {
        margin-bottom: 0;
      }

      .form__label {
        width: 260px;
        margin-bottom: 0;

        &--big {
          width: 414px;
        }
      }

      .form__input {
        margin-top: 3px;
      }
    }

    .clients {
      padding-top: 80px;
      background-color: var(--gray-200);
      padding-bottom: clamp(1.25rem, 0.591rem + 3.3vw, 3.125rem);
    }

    blockquote::before {
      display: none;
      padding-left: 0;
    }

  }

  &-about {

    .page-hero.in-view {
      @include my-reveal-bg($anim-name: my-revealBg-50-100);
    }

    .clients {
      // padding-top: 120px;
      padding-top: clamp(2.5rem, 1.621rem + 4.4vw, 5rem);
      // padding-top: 80px;
      background-color: var(--white);
    }

    .values {
      padding-top: clamp(2.5rem, 1.621rem + 4.4vw, 5rem);
      // padding-top: 80px;
      background-color: var(--gray-200);
    }

    blockquote::before {
      top: 0;
    }
  }

  &-positions {
    .page-hero.in-view {
      @include my-reveal-bg($anim-name: my-revealBg-52-85);
    }
  }

  &-news {
    .page-hero.in-view {
      @include my-reveal-bg($anim-name: my-revealBg-56-100);
    }
  }

  &-services {
    .page-hero.in-view {
      @include my-reveal-bg($anim-name: my-revealBg-50-100);
    }
  }

  &-services-inside {
    .page-hero {
      @include my-reveal-bg($anim-name: my-revealBg-52-100);
    }


    .clients {
      padding-top: 120px;
      padding-bottom: 120px;
      background-color: var(--gray-200);

      &__title {
        display: none;
      }
    }

    .similar {
      display: none;
    }
  }

  &-contacts {
    .header {
      background-color: transparent;
    }

    .page-hero.in-view {
      @include my-reveal-bg($anim-name: my-revealBg-46-100);
    }
  }



  &-work {
    .page-hero.in-view {
      @include my-reveal-bg($duration: 1s,
        $timing: ease-out,
        $start-size: 0% 100%,
        $anim-name: my-revealBg-46-100);

    }

    .page-hero__content {
      margin-bottom: 40px;
    }

    .testimonials {
      padding-top: 80px;
    }

    .clients {
      padding-top: 172px;

      .clients__ticker {
        padding-bottom: 0;
      }

    }

    .clients__ticker {
      padding-bottom: 100px;
    }

    blockquote::before {
      display: none;
      padding-left: 0;
    }
  }

  &-project {
    .page-hero {
      height: auto;
      background-color: var(--white);
      padding-bottom: 0;
    }

    .page-hero__title {
      position: absolute;
      white-space: nowrap;
      width: 1px;
      height: 1px;
      overflow: hidden;
      border: 0;
      padding: 0;
      clip: rect(0 0 0 0);
      clip-path: inset(50%);
      margin: -1px;
    }

    .related {
      display: none;
    }
  }

  &-post {
    .page-hero {
      @media screen and (max-width:1025px) {
        margin-bottom: 40px;
      }
    }
  }
}